.Form {
  width: 30vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 2.5vh;
  
} 
@media all and (max-width: 768px)  { 
  .Form {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin-bottom: 2.5vh;
    
  } 
}